
.backgound img{
    width: 100%;
    height: 80vh;
}
.backgound .slider{
    width: 90%;
    margin-left: 20px;
}
nav{
    padding: 15px 0px;
    width: 100%;
    z-index: 99999999999;
    position: fixed;
    top: 0%;
    background-image: linear-gradient(#CE362B ,#D23A2C);
}
nav .container{
    display: flex;
    justify-content: space-between;
}
nav .container .logo {
    display: flex;
    justify-content: center;
    align-items: center;
}
nav .container .logo img{
    width: 80px;
}
nav .container ul{
    list-style: none;
    display: flex; 
}
nav .container ul li{
    margin-left: 40px;
    cursor: pointer;
    font-family: IBM;
    color: white;
}
nav .container ul li:hover{
    color: #F2CB05;
}
nav  .container .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
}
nav  .container .icon svg {
    color: white;
    font-size: 20px;
    z-index: 999;
    top: 0;
    padding: 10px;
}
nav  .container .icon svg:first-child {
    display: none;
}
/*responsive */


@media (max-width: 768px) {
    nav {
      padding: 20px;
    }
    
  
    nav .menu {
      display: flex;
      gap: 30px;
      position: absolute;
      top: -20px;
      right: -100%;
      width: 30vw;
      min-width: 200px;
      height: 100vh;
      background-color: #ba2020;
      z-index: 999;
      flex-direction: column;
      padding: 20px;
      border-radius: 10px 0 0 10px;
      transition: 0.3s ease-in-out;
    }
    
  
    nav .menu.active {
      right: 0;
    }
  
    nav .icon .menu-icon{
      z-index: 9;
    }
  }