    transition: .5s;

}
.wraper .container .wraper-img .image2{
    background-image: url(/static/media/h1-bn2.2faaad58.jpg);
    background-size: cover;
    background-position:center;
    background-repeat: no-repeat;
    transition: .5s;
    
}
.wraper .container .wraper-img .content{
        padding: 40px 20px ;
